import Vue from 'vue'
import Router from 'vue-router'
import { components, AmplifyEventBus } from 'aws-amplify-vue'
import * as AmplifyModules from 'aws-amplify'
import { AmplifyPlugin } from 'aws-amplify-vue'
import store from '@/store'
import About from '@/components/About.vue'
import CheckIn from '@/components/CheckIn.vue'
import PreCheckIn from '@/components/PreCheckIn.vue'

Vue.use(Router)
Vue.use(AmplifyPlugin, AmplifyModules)

let user
// getUser().then((user) => {
//   if (user) {
//     router.push({path: '/'})
//   }
// })

AmplifyEventBus.$on('authState', async (state) => {
  if (state === 'signedOut'){

    user = null
    store.dispatch('amplify/setUser', null)
    store.dispatch('amplify/setUserIdentityId', null)

    router.push({path: '/auth'})
  } else if (state === 'signedIn') {
    console.log('signedIn getUser')
    user = await getUser()
    
    const rtn = await store.dispatch('checkIn/loadGuests', store.state.amplify.user.username)
      .catch(e => e)

    if (rtn) {
      console.error(rtn)
      window.alert(rtn.message)

      await AmplifyModules.Auth.signOut()
      AmplifyEventBus.$emit('authState', 'signedOut')

    } else {
      router.push({path: '/'})
    }
  }

});

async function getUser() {
  try {
    // const currentAuthenticatedUser = await Vue.prototype.$Amplify.Auth.currentAuthenticatedUser()
    // const currentUserInfo = await Vue.prototype.$Amplify.Auth.currentUserInfo()

    const currentAuthenticatedUser = await AmplifyModules.Auth.currentAuthenticatedUser()
    const currentUserInfo = await AmplifyModules.Auth.currentUserInfo()

    console.log('currentAuthenticatedUser', currentAuthenticatedUser)
    console.log('currentUserInfo', currentUserInfo)

    if (currentAuthenticatedUser && currentAuthenticatedUser.signInUserSession) {
      // AmplifyStore.commit('setUser', currentAuthenticatedUser)
      // AmplifyStore.commit('setUserIdentityId', currentUserInfo.id)

      store.dispatch('amplify/setUser', currentAuthenticatedUser)
      store.dispatch('amplify/setUserIdentityId', currentUserInfo.id)
    } 
    return currentAuthenticatedUser

  } catch (e) {
    console.log('getUser error', e)

    store.dispatch('amplify/setUser', null)
    store.dispatch('amplify/setUserIdentityId', null)

    return null
  }
}


const router = new Router({
  mode: 'hash',
  // base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: About,
      meta: { requiresAuth: true }
    },
    {
      path: '/checkIn',
      name: 'checkIn',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: CheckIn,
      meta: { requiresAuth: true }
    },
    {
      path: '/preCheckIn',
      name: 'preCheckIn',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: PreCheckIn,
      meta: { requiresAuth: true }
    },
    {
      path: '/auth',
      name: 'Authenticator',
      component: components.Authenticator
    }
  ]
})

router.beforeResolve(async (to, from, next) => {
  console.log('beforeResolve to:', to.fullPath)
  if (to.matched.some(record => record.meta.requiresAuth)) {
    console.log('beforeResolve getUser')
    console.log(JSON.stringify(store.state.checkIn))


    user = await getUser()
    if (!user) {
      return next({
        path: '/auth',
        query: {
          redirect: to.fullPath
        }
      })
    }
    return next()
  }
  return next()
})

export default router