<template>
  
  <v-stepper v-model="currentStep">
    <camera-dialog @captureClose="onCaptureClose" @cancelClose="onCancelClose"></camera-dialog>

    <v-stepper-header>
      <template v-for="n in steps">
        <v-stepper-step
          :complete="currentStep > n"
          :key="`${n}-step`"
          :step="n"
          editable
        >
          Guest No.{{ n }}
        </v-stepper-step>

        <v-divider
          v-if="n !== steps"
          :key="n"
        ></v-divider>
      </template>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content
        v-for="n in steps"
        :key="`${n}-content`"
        :step="n"
      >
      <v-layout row fill-height>
        <v-flex xs6>
          <span class="headline"><v-btn class="headline" flat @click="showDialog(true)">{{ $t("checkIn.titleId") }}</v-btn></span>
        </v-flex>
        <v-flex xs6>
          <span class="headline"><v-btn class="headline" flat @click="showDialog(false)">{{ $t("checkIn.titleFace") }}</v-btn></span>
        </v-flex>
      </v-layout>  
      <v-layout row fill-height>
        <v-flex xs12>
          <v-container fluid>
            <v-layout row justify-space-between>
              <v-flex xs6>
                <img :src="idImgSrc" class="img-responsive" width="80%" @click="showDialog(true)" style="cursor: pointer;"/>
              </v-flex>
              <v-flex xs6>
                <img :src="faceImgSrc" class="img-responsive" width="80%" @click="showDialog(false)" style="cursor: pointer;"/>
              </v-flex>
            </v-layout>
          </v-container>
        </v-flex>
      </v-layout>

      <v-layout row fill-height>
        <v-flex xs12>
          <v-container fluid>
            <v-layout row justify-space-between>
              <v-flex xs3>
                <v-text-field label="*First name" required hint="*" v-model="guest.firstName" v-if="guest" ></v-text-field>
              </v-flex>
              <v-flex xs3>
                <v-text-field label="Middle name"></v-text-field> 
              </v-flex>
              <v-flex xs3>
                <v-text-field label="*Last name" required hint="*" v-model="guest.lastName" v-if="guest" ></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
          <small>*indicates required field</small>
        </v-flex>
      </v-layout>

      <v-layout row fill-height>
        <v-flex xs12>
          <v-spacer></v-spacer>
          <!-- <v-btn color="blue darken-2" flat @click="showDialog(false)">Take Picture</v-btn> -->
          <v-btn class="headline" icon @click="prevStep(n)">
            <v-icon x-large>navigate_before</v-icon>
          </v-btn>
          <v-btn class="headline" flat @click="verify()">{{ $t("checkIn.verifyId") }}</v-btn>
          <v-btn class="headline" icon @click="nextStep(n)">
            <v-icon x-large>navigate_next</v-icon>
          </v-btn>
        </v-flex>
      </v-layout>  

      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import CameraDialog from '@/components/CameraDialog.vue'
import { EventBus } from '@/eventBus'
import { Storage } from 'aws-amplify'
import aws_exports from '@/aws-exports'
import { mapState, mapGetters, mapActions } from 'vuex'


export default {
  name: 'CheckIn',
  data () {
    return {}
  },
  created () {
  },
  computed: {
    currentStep: {
      get() {
        return this.$store.state.checkIn.currentStep
      },
      set (val) {
        return this.setCurrentStep(val)
      }
    },
    ...mapState({
      userIdentityId: state => state.amplify.userIdentityId,
      guests: state => state.checkIn.guests,
      faceImgSrc: state => state.checkIn.faceImgSrc,
      idImgSrc: state => state.checkIn.idImgSrc
    }),
    ...mapGetters('checkIn', {
      steps: 'steps',
      guest: 'guest'
    })
  },
  methods: {
    prevStep (n) {
      if (n === 1) {
        this.setCurrentStep(this.steps)

      } else {
        this.setCurrentStep(n - 1)
      }
    },
    nextStep (n) {
      if (n === this.steps) {
        this.setCurrentStep(1)
      } else {
        this.setCurrentStep(n + 1)
      }
    },
    async verify() {
      await this.saveGuest()

      if ( this.guest.idBase64Img ) {
        await this.putImage(true);
      }

      if ( this.guest.faceBase64Img ) {
        await this.putImage(false);
      }

      await this.verifyId()

      // console.log('guest', JSON.stringify(this.guest))
      window.alert(`similarity: ${this.guest.similarity}\nmessageCode: ${this.guest.messageCode}`)
    },
    showDialog(isId) {
      EventBus.$emit('dialog', { showDialog: true, isId });
      this.isId = isId;
    },
    onCancelClose() {
      console.log('cancelClose')
    },
    async onCaptureClose(val) {
      if (val.base64Img) {
        if (val.isId) {
          this.guest.idBase64Img = val.base64Img
          this.guest.idImgSaved = true
          await this.setIdImgSrc()
        } else {
          this.guest.faceBase64Img = val.base64Img
          this.guest.faceImgSaved = true
          await this.setFaceImgSrc()
        }
      }
    },
    ...mapActions({
      setCurrentStep: 'checkIn/setCurrentStep',
      setIdImgSrc: 'checkIn/setIdImgSrc',
      setFaceImgSrc: 'checkIn/setFaceImgSrc',
      saveGuest: 'checkIn/saveGuest',
      putImage: 'checkIn/putImage',
      verifyId: 'checkIn/verifyId'
    })
  },
  components: {
    cameraDialog: CameraDialog
  }
}
</script>