<template>
  <v-toolbar app height="32px" v-if="user">
    <!-- <v-img :src="require('../assets/logo.png')" width="100%" height="100%"></v-img> -->
    <img :src="require('../assets/img/logo.svg')" class="img-responsive" height="24"/>
    

    <v-spacer></v-spacer>
    <v-toolbar-items>
      <v-menu :nudge-width="100">
        <v-toolbar-title slot="activator">
          <v-icon>language</v-icon>
          <!-- <span>All</span> -->
        </v-toolbar-title>

        <v-list>
          <v-list-tile
            v-for="item in items"
            :key="item"
            @click="switchLocale(item)"
          >
            <v-list-tile-title>{{ $t( "menu." + item) }}</v-list-tile-title>
            <flag :iso="item" />
          </v-list-tile>
        </v-list>
      </v-menu>

      <v-btn icon @click.native="signOut">
        <v-icon>exit_to_app</v-icon>
      </v-btn>

    </v-toolbar-items>
  </v-toolbar>
</template>

<script>
import Vue from 'vue'
import { mapGetters, mapState } from 'vuex'
import FlagIcon from 'vue-flag-icon'
import { AmplifyEventBus } from 'aws-amplify-vue'
import * as AmplifyModules from 'aws-amplify'

Vue.use(FlagIcon)

export default {
  name: 'Menu',
  data: () => ({
    items: [
      'gb', 'hk', 'jp'
    ]
  }),
  computed: mapState({
    user: state => state.amplify.user
  }),
  methods: {
    switchLocale (item) {
      this.$i18n.locale = item
    },
    async signOut () {
      await AmplifyModules.Auth.signOut()
      AmplifyEventBus.$emit('authState', 'signedOut')
    }
  }
}
</script>