import appsync from '@/api/appsync'
import storage from '@/api/storage'
import aws_exports from '@/aws-exports'

const state = {
  guests: [],
  // steps: 1
  currentStep: 100,
  idImgSrc: null,
  faceImgSrc: null
}

const mutations = {
  setGuests(state, val) {
    state.guests = val
  },
  setCurrentStep(state, val) {
    state.currentStep = val
  },
  setIdImgSrc(state, val) {
    state.idImgSrc = val
  },
  setFaceImgSrc(state, val) {
    state.faceImgSrc = val
  }
}

const actions = {
  async loadGuests ({ commit, rootState }, val) {
    const reservation = await appsync.retrieveReservation(val)

    // need to show error dialog here
    if (!reservation) {
      // need to show error dialog here
      // console.error('No Reservation Available')
      throw new Error('No Reservation Available!');
    }

    const guests = await appsync.retrieveGuests(val)

    if (guests.length < reservation.guestCount) {
      let i = guests.length

      for (i; i < reservation.guestCount; i++) {
        guests.push({ 
          reservationCode: val, 
          guestNo: i, 
          firstName: ' ', 
          lastName: ' ',
          imgBucket: aws_exports.aws_user_files_s3_bucket,
          idImgName: `id/${i}.jpg`,
          idImgKey: `private/${rootState.amplify.userIdentityId}/id/${i}.jpg`,
          idImgSaved: false,
          faceImgName: `face/${i}.jpg`,
          faceImgKey: `private/${rootState.amplify.userIdentityId}/face/${i}.jpg`,
          faceImgSaved: false,
          lastUpdateOn: null,
          idBase64Img: null,
          faceBase64Img: null
        })
      }
    }

    let i = 0
    for (i; i < guests.length; i++) {
      guests[i].idBase64Img = null
      guests[i].faceBase64Img = null
    }

    console.log('loadGuests', guests)

    commit('setGuests', guests)
    commit('setCurrentStep', 1)

    return

  },
  async saveGuest ({ commit, getters }) {
    if (getters.guest.lastUpdateOn) {
      getters.guest.lastUpdateOn = Math.round((new Date).getTime() / 1000)
      const reservation = await appsync.updateReservationGuest(getters.guest)
    } else {
      getters.guest.lastUpdateOn = Math.round((new Date).getTime() / 1000)
      const reservation = await appsync.createReservationGuest(getters.guest)
    }
  },
  async putImage ({ commit, getters}, isId) {
    if (isId) {
      await storage.put(getters.guest.idImgName, getters.guest.idBase64Img.split(',')[1])
    } else {
      await storage.put(getters.guest.faceImgName, getters.guest.faceBase64Img.split(',')[1])
    }
  },
  setCurrentStep ({ commit, dispatch }, val) {
    commit('setCurrentStep', val)
    dispatch('setIdImgSrc')
    dispatch('setFaceImgSrc')

  },
  async setIdImgSrc ({ commit, getters, state }) {
    // console.log('state.guests', state.guests)
    // console.log('getters.guest', getters.guest)

    if (getters.guest) {
      if (getters.guest.idBase64Img) {
        commit('setIdImgSrc', getters.guest.idBase64Img)
      } else {
        if (getters.guest.idImgSaved) {
          const url = await storage.get(getters.guest.idImgName)
          commit('setIdImgSrc', url)
        } else {
          commit('setIdImgSrc', require('@/assets/img/id.svg'))
        }        
      }
    } else {
      commit('setIdImgSrc', require('@/assets/img/id.svg'))
    }
  },
  async setFaceImgSrc ({ commit, getters }) {

    if (getters.guest) {
      if (getters.guest.faceBase64Img) {
        commit('setFaceImgSrc', getters.guest.faceBase64Img)
      } else {
        if (getters.guest.faceImgSaved) {
          const url = await storage.get(getters.guest.faceImgName)
          commit('setFaceImgSrc', url)
        } else {
          commit('setFaceImgSrc', require('@/assets/img/face.svg'))
        }        
      }
    } else {
      commit('setFaceImgSrc', require('@/assets/img/face.svg'))
    }
  },
  async verifyId ({ commit, getters}) {
    const res = await appsync.compareFaces({
      imgBucket: getters.guest.imgBucket,
      idImgKey: getters.guest.idImgKey,
      faceImgKey: getters.guest.faceImgKey,
      similarityThreshold: 50
    })

    getters.guest.passed = res.passed
    getters.guest.similarity = res.similarity
    getters.guest.messageCode = res.messageCode
  }
}

const getters = {
    steps: state => {
      return state.guests.length
    },
    guest: state => {
      return state.guests[state.currentStep - 1]
    },
    currentStep: state => {
      return state.currentStep
    },
    idImgSrc: state => {
      return state.idImgSrc
    },
    faceImgSrc: state => {
      return state.faceImgSrc
    }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}