export const compareFaces = `query CompareFaces($imgBucket: String!, $idImgKey: String!, $faceImgKey: String!, $similarityThreshold: Float!) {
  compareFaces(imgBucket: $imgBucket, idImgKey: $idImgKey, faceImgKey: $faceImgKey, similarityThreshold: $similarityThreshold) {
    passed
    similarity
    messageCode
  }
}
`;

export const getReservation = `query GetReservation($reservationCode: String!) {
  getReservation(reservationCode: $reservationCode) {
    reservationCode
    guestCount
    checkInDate
    checkOutDate
  }
}
`;

export const getReservationGuests = `query GetReservationGuests($reservationCode: String!) {
  getReservationGuests(reservationCode: $reservationCode) {
    reservationCode
    guestNo
    lastName
    firstName
    imgBucket
    faceImgName
    faceImgKey
    faceImgSaved
    idImgName
    idImgKey
    idImgSaved
    lastUpdateOn
  }
}
`;

export const createReservationGuest = `mutation createReservationGuest($reservationCode: String!, $guestNo: Int!, $lastName: String, $firstName: String,  $imgBucket: String, $faceImgName: String, $faceImgKey: String, $faceImgSaved: Boolean, $idImgName: String, $idImgKey: String, $idImgSaved: Boolean, $lastUpdateOn: Int!) {
  createReservationGuest(input: { reservationCode: $reservationCode, guestNo: $guestNo, lastName: $lastName, firstName: $firstName, imgBucket: $imgBucket, faceImgName: $faceImgName, faceImgKey: $faceImgKey, faceImgSaved: $faceImgSaved, idImgName: $idImgName, idImgKey: $idImgKey, idImgSaved: $idImgSaved, lastUpdateOn: $lastUpdateOn }) {
    reservationCode
    guestNo
    lastName
    firstName
    imgBucket
    faceImgName
    faceImgKey
    faceImgSaved
    idImgName
    idImgKey
    idImgSaved
    lastUpdateOn
  }
}
`;


